import React,{Component} from 'react';
import {Link} from 'react-router-dom';


const addrest = "https://restaurentapi.herokuapp.com/addrestaurent";

class AddRest extends Component{
    constructor(){
        super()

        this.state={
            _id:'',
            name:'',
            city_name:'',
            city:'',
            area:'',
            locality:'',
            cost:'',
            address:'',
            contact_number:'',
            type:[
                {
                    mealtype:'',
                    name:''
                },
                {
                    mealtype:'',
                    name:''
                }
            ],
            Cuisine:[
                {
                    cuisine:'',
                    name:''
                },
                {
                    cuisine:'',
                    name:''
                }
            ]
        }

    }

    handleChangeID = (event) => {
        this.setState({_id:event.target.value})
    }

    handleChangeRestName =(event) => {
        this.setState({name:event.target.value})
    }

    handleChangeCityName =(event) =>{
        this.setState({city_name:event.target.value})
    }

    handleChangeCity = (event) => {
        this.setState({city:event.target.value})
    }

    handleChangeArea =(event) => {
        this.setState({area:event.target.value})
    }

    handleChangeLocality =(event) =>{
        this.setState({locality:event.target.value})
    }

    handleChangeCost =(event) => {
        this.setState({cost:event.target.value})
    }

    handleChangeAddress =(event) =>{
        this.setState({address:event.target.value})
    }

    handleChangeContact =(event) =>{
        this.setState({contact_number:event.target.value})
    }

    handleChangeMealId1 = (event) => {
        this.state.type[0].mealtype = event.target.value
        this.forceUpdate()
    }

    handleChangeMealName1 =(event) => {
        this.state.type[0].name = event.target.value
        this.forceUpdate()
    }

    handleChangeMealId2 =(event) =>{
        this.state.type[1].mealtype = event.target.value
        this.forceUpdate()
    }

    handleChangeMealName2 =(event) => {
        this.state.type[1].name = event.target.value
        this.forceUpdate()
    }

    handleChangeCuisineId1 =(event) =>{
        this.state.Cuisine[0].cuisine = event.target.value
        this.forceUpdate()
    }

    handleChangeCuisineName1 =(event) =>{
        this.state.Cuisine[0].name = event.target.value
        this.forceUpdate()
    }

    handleChangeCuisineId2 =(event) =>{
        this.state.Cuisine[1].cuisine = event.target.value
        this.forceUpdate()
    }

    handleChangeCuisineName2 =(event) =>{
        this.state.Cuisine[1].name = event.target.value
        this.forceUpdate()
    }

    handleSubmit = () => {
       fetch(addrest,{
           method:'POST',
           headers:{
               'Accept':'application/json',
               'Content-Type':'application/json'
           },
           body:JSON.stringify(this.state)
       })
       .then((this.props.history.push("/admin/addrest")))
       alert("Your Restaurent Added sussesfully")
    }


    render(){
        return(
            <div className="container">
                <div className="panel panel-success">
                    <div className="panel-heading">
                        <center><h3>ADD NEW RESTAURENT</h3></center>
                    </div>
                    <div className="panel-body">
                        <div className="form-group">
                            <label>_Id</label>
                            <input type="text" name="_id" value={this.state._id} 
                            className="form-control" onChange={this.handleChangeID}/>
                        </div>
                        <div className="form-group">
                            <label>Restaurent Name</label>
                            <input type="text" name="name" value={this.state.name}
                            className="form-control" onChange={this.handleChangeRestName}/>
                        </div>
                        <div className="form-group">
                            <label>City Name</label>
                            <input type="text" name="city_name" value={this.state.city_name}
                            className="form-control" onChange={this.handleChangeCityName}/>
                        </div>
                        <div className="form-group">
                            <label>City Id</label>
                            <input type="text" name="city" value={this.state.city}
                            className="form-control" onChange={this.handleChangeCity}/>
                        </div>
                        <div className="form-group">
                            <label>Area ID</label>
                            <input type="text" name="area" value={this.state.area}
                            className="form-control" onChange={this.handleChangeArea}/>
                        </div>
                        <div className="form-group">
                            <label>Locality</label>
                            <input type="text" name="locality" value={this.state.locality}
                            className="form-control" onChange={this.handleChangeLocality}/>
                        </div>
                        <div className="form-group">
                            <label>cost</label>
                            <input type="text" name="cost" value={this.state.cost}
                            className="form-control" onChange={this.handleChangeCost}/>
                        </div>
                        <div className="form-group">
                            <label>Address</label>
                            <input type="text" name="address" value={this.state.address}
                            className="form-control" onChange={this.handleChangeAddress}/>
                        </div>
                        <div className="form-group">
                            <label>Contact number</label>
                            <input type="text" name="contact_number" value={this.state.contact_number}
                            className="form-control" onChange={this.handleChangeContact}/>
                        </div>
                        <div className="form-group">
                            <label>Mealtype ID(1)</label>
                            <input type="text" name="mealtype1" value={this.state.type[0].mealtype}
                            className="form-control"  onChange={this.handleChangeMealId1}/>
                        </div>
                        <div className="form-group">
                            <label>Mealtype Name(1)</label>
                            <input type="text" name="mname1" value={this.state.type[0].name}
                            className="form-control" onChange={this.handleChangeMealName1}/>
                        </div>
                        <div className="form-group">
                            <label>Mealtype ID(2)</label>
                            <input type="text" name="mealtype2" value={this.state.type[1].mealtype}
                            className="form-control"  onChange={this.handleChangeMealId2}/>
                        </div>
                        <div className="form-group">
                            <label>Mealtype Name(2)</label>
                            <input type="text" name="mname2" value={this.state.type[1].name}
                            className="form-control"  onChange={this.handleChangeMealName2}/>
                        </div>
                        <div className="form-group">
                            <label>cuisine ID (1)</label>
                            <input type="text" name="mealtype1" value={this.state.Cuisine[0].cuisine}
                            className="form-control"  onChange={this.handleChangeCuisineId1}/>
                        </div>
                        <div className="form-group">
                            <label>Cuisine Name(1)</label>
                            <input type="text" name="mname1" value={this.state.Cuisine[0].name}
                            className="form-control" onChange={this.handleChangeCuisineName1}/>
                        </div>
                        <div className="form-group">
                            <label>cuisine ID 2</label>
                            <input type="text" name="mealtype1" value={this.state.Cuisine[1].cuisine}
                            className="form-control"  onChange={this.handleChangeCuisineId2}/>
                        </div>
                        <div className="form-group">
                            <label>Cuisine Name(2)</label>
                            <input type="text" name="mname1" value={this.state.Cuisine[1].name}
                            className="form-control" onChange={this.handleChangeCuisineName2}/>
                        </div>
                        <center>
                        <Link to="/admin" className="btn btn-danger">Back</Link>&nbsp;
                        <button className="btn btn-success" onClick={this.handleSubmit}>ADD</button>
                        </center>
                    </div>
                </div>
            </div>
        )
    }
}

export default AddRest;