import React,{Component} from 'react';
import axios from 'axios';
import ListingDisplay from './ListingDisplay';
import CostFilter from './costfilter';
import CuisineFilter from './Cuisinefilter';
import CityFilter from './Cityfilter';
import SortFilter from './Sortfilter';
import './Listing.css';

const url = "https://restaurentapi.herokuapp.com/restaurents?mealtype=";
const mealurl = "https://restaurentapi.herokuapp.com/mealtype"


var limit = 3
class ListingApi extends Component{
    constructor(props){
        super()

        this.state={
            hotelist:'',
            mealname:'',
            activePage: 1,
            totalNoOfItems: 1,
        }
    }

    setDataAsPerFilter(sortedData){
        this.setState({hotelist:sortedData})
        if(this.state.hotelist=='')
        {
            alert("No Restaurent Available")
        }
    }

    render(){
        return(
            <div className="container">
                <div className="row">
                    <div className="col-md-3 col-sm-1 col-xs-1">
                        <div className="Rectangle-5">
                            <div className="filter-content">
                                <div className="filter-heading">
                                    Filters
                                </div>
                                <div className="content">
                                    Select Location
                                </div>
                                <CityFilter citydata={(data) => {this.setDataAsPerFilter(data)}}/>
                                <div className="content">
                                    Cuisine
                                </div>
                                <CuisineFilter cuisinedata={(data) => {this.setDataAsPerFilter(data)}}/>
                                <div className="content">
                                    <span>Cost For Two</span>
                                </div>
                                <CostFilter costdata={(data) => {this.setDataAsPerFilter(data)}}/>
                                <div className="content">
                                    Sort
                                </div>
                                <SortFilter sortdata={(data) => {this.setDataAsPerFilter(data)}}/>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-9 col-sm-11 col-xs-11">
                        <center>
                            <h3 style={{color:"darkblue",fontWeight:600}}>Exclusive {this.state.mealname} Option</h3>
                        </center>
                        <ListingDisplay listdata={this.state.hotelist} activePage={this.state.activePage} limit={limit} totalNoOfItems={this.state.totalNoOfItems} pageNumber={(data) => {this.setState({activePage:data})}}/>
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount(){
       var mealid = this.props.match.params.id
       sessionStorage.setItem('mealid',mealid)
       axios.get(`${mealurl}`)
       .then((response) => {
           for(var i=0;i<response.data.length;i++){
                if(response.data[i]._id == Number(mealid)){
                    this.setState({mealname:response.data[i].name})
                    i = response.data.length;
                }
            }
       })
       fetch(`${url}${mealid}`)
       .then((res) => res.json())
       .then((data) =>  {this.setState({hotelist: data.slice(0, data.length - 1),totalNoOfItems:data.length - 1})})
    }
}

export default ListingApi;