import React,{Component} from 'react';
import {withRouter} from 'react-router-dom';
import axios from 'axios';
import './Listing.css';

const lurl = "https://restaurentapi.herokuapp.com/location";
const cityurl="https://restaurentapi.herokuapp.com/restaurantList";

class CityFilter extends Component{
    constructor(){
        super()

        this.state={
            location:'',
        }
    }

    renderCity = (data) => {
        if(data){
            return data.map((item) => {
                return(
                    <option value={item.city} key={item._id}>
                        {item.city_name}
                    </option>
                )
            })
        }
    }


    // Load restaurent wrt to City 
    handleCity = (event) => {
        const cityid = event.target.value
        let mealType = sessionStorage.getItem('mealid');
        let curl;
        if(cityid==''){
            curl= `${cityurl}/${mealType}`
        }else{
            curl= `${cityurl}/${mealType}?city=${cityid}`
        }
        axios.get(curl)
        .then((response) => {this.props.citydata(response.data)})
    }

    render(){
        return(
            <React.Fragment>
                <select className="select-location-dropdown filter" onChange={this.handleCity}>
                    <option value="">Select City</option>
                    <option value="">All City</option>
                    {this.renderCity(this.state.location)}
                </select>
            </React.Fragment>
        )
    }

    componentDidMount(){
        fetch(lurl,{Method:'GET'})
        .then((res) =>  res.json())
        .then((data) => {this.setState({location:data})})
    }
}

export default withRouter(CityFilter);