import React from 'react';
import {Link} from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import './RestDisplay.css';

const RestaurentList = (props) => {
    const renderHotel = ({restData})=>{
        if(restData){
            return(
                <div className="container">
                    <div className="panel">
                        <div className="panel-body">
                            <div className="row">
                                <div className="col-md-12">
                                    <button type="button"  className="img-btn" data-toggle="modal" data-target="#myModal">
                                        <img className="img-responsive" src={restData.thumb}
                                        style={{width:1500,height:400}} alt={restData.name}/>
                                    </button>
                                    <div id="myModal" className="modal fade" role="dialog">
                                        <div className="modal-dialog">
                                            <div className="modal-content">
                                                <div className="modal-header">
                                                    <button type="button" className="close" data-dismiss="modal">&times;</button>
                                                    <h4 className="modal-title">
                                                        Image Gallery For {restData.name}
                                                    </h4>
                                                </div>
                                                <div className="modal-body">
                                                    <div id="myCarousel" className="carousel slide" data-ride="carousel">
                                                        <ol className="carousel-indicators">
                                                            <li data-target="#myCarousel" data-slide-to="0" className="active"></li>
                                                            <li data-target="#myCarousel" data-slide-to="1"></li>
                                                            <li data-target="#myCarousel" data-slide-to="2"></li>
                                                        </ol>
                                                        <div className="carousel-inner">
                                                            <div className="item active">
                                                            <img className="img-responsive" src={restData.thumb}
                                                        style={{width:1500,height:400}} alt={restData.name}/>
                                                            </div>

                                                            <div className="item">
                                                            <img className="img-responsive" src={restData.thumb}
                                                        style={{width:1500,height:400}} alt={restData.name}/>
                                                            </div>

                                                            <div className="item">
                                                            <img className="img-responsive" src={restData.thumb}
                                                        style={{width:1500,height:400}} alt={restData.name}/>
                                                            </div>
                                                        </div>
                                                        <a className="left carousel-control" href="#myCarousel" data-slide="prev">
                                                            <span className="glyphicon glyphicon-chevron-left"></span>
                                                            <span className="sr-only">Previous</span>
                                                        </a>
                                                        <a className="right carousel-control" href="#myCarousel" data-slide="next">
                                                            <span className="glyphicon glyphicon-chevron-right"></span>
                                                            <span className="sr-only">Next</span>
                                                        </a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-12 head">
                                    {restData.name}
                                </div>
                            </div>
                            <Tabs>
                                <TabList className="tablist">
                                    <Tab>Overview</Tab>
                                    <Tab>Contact</Tab>
                                </TabList>
                                <hr id="hrs"/>
                                <TabPanel className="tabpanel">
                                    <div>
                                        <div className="about">
                                            About This Place
                                        </div>
                                        <div className="cuisine">
                                            Cuisine
                                        </div>
                                        <div className="Bakery-Fast-food">
                                            {restData.Cuisine[0].name} | {restData.Cuisine[1].name}
                                        </div>
                                        <div className="averagecost">
                                            Average Cost
                                        </div>
                                        <div className="-for-two-people-approx">
                                             {restData.cost} for two people approx
                                        </div>
                                    </div>
                                </TabPanel>
                                <TabPanel className="tabpanel">
                                    <div>
                                        <div className="phone">
                                            Phone No
                                        </div>
                                        <div className="Number">
                                            +91 {restData.contact_number} 
                                        </div>
                                        <div className="big-chill">
                                            {restData.name}
                                        </div>
                                        <div className="address">
                                            {restData.address}
                                        </div>
                                    </div>
                                </TabPanel>
                            </Tabs>
                            <div className="row">
                                <center>
                                    <Link to={`/list/${sessionStorage.getItem('mealid')}`} className="btn btn-danger">Back</Link>&nbsp;&nbsp;
                                    <Link to={`/booking/${restData._id}`} className="btn btn-success">Place Order</Link>
                                </center>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }else{
            return(
                <div>
                    <center><img src="/images/loading.gif" alt="loadericon"/></center>
                </div>
            )
        }
    }
    return(
        <React.Fragment>
           {renderHotel(props)}
        </React.Fragment>
    )
}

export default RestaurentList;