import React,{Component} from 'react';
import {Link} from 'react-router-dom';

const resturl = "https://restaurentapi.herokuapp.com/restaurantdetails";

class SearchUpdateRest extends Component{
    constructor(){
        super()

        this.state={
            _id:''
        }

    }

    handleChangeID = (event) => {
        this.setState({_id:event.target.value})

    }   

    render(){
        return(
            <div className="container">
                <div className="panel panel-success">
                    <div className="panel-heading">
                        <center><h3>Search RESTAURENT</h3></center>
                    </div>
                    <div className="panel-body">
                        <div className="form-group">
                            <label>_Id</label>
                            <input type="text" name="_id" value={this.state._id} 
                            className="form-control" onChange={this.handleChangeID}/>
                        </div>
                        <center>
                        <Link to="/admin" className="btn btn-danger">Back</Link>&nbsp;
                        <Link to={`/admin/updaterest/${this.state._id}`} className="btn btn-success">Search</Link>
                        </center>
                    </div>
                </div>
            </div>
        )
    }
}

export default SearchUpdateRest;