import React,{Component} from 'react';


const delurl = "https://restaurentapi.herokuapp.com/deleterest";

class DeleteRest extends Component{
    constructor(){
        super()

        this.state={
            _id:''
        }
    }

    handleChangeID =(event)=>{
        this.setState({_id:event.target.value})
    }

    handleDelete =()=>{
        fetch(delurl,{
            method:'DELETE',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(this.state)
        })
        .then((this.props.history.push("/admin")))
        alert("Your Restaurent Deleted sussesfully")
    }

    render(){
        return(
            <React.Fragment>
                <div className="container">
                <div className="panel panel-success">
                    <div className="panel-heading">
                        <center><h3>DELETE RESTAURENT</h3></center>
                    </div>
                    <div className="panel-body">
                        <div className="form-group">
                            <label>_Id</label>
                            <input type="text" name="_id" value={this.state._id} 
                            className="form-control" onChange={this.handleChangeID}/>
                        </div>
                        <button className="btn btn-danger" onClick={this.handleDelete} >Delete</button>
                    </div>
                </div>
                </div>
            </React.Fragment>
        )
    }

}

export default DeleteRest;