import React  from 'react';
import {Link} from 'react-router-dom';
import './Listing.css';
import Pagination from "react-js-pagination";


const renderList = (props,listdata) => {
    if(listdata){
        var limit = props.limit;
        var page = props.activePage;
        listdata = listdata.slice((page - 1) * limit, (page - 1) * limit + limit);
        return listdata.map((item) => {
            return(
                <Link to={`/details/${item._id}`} key={item._id}>
                    <div className="item" key={item._id}>
                        <div className="row">
                            <div className="col-md-3 col-xs-4">
                                <img className="image" src={item.thumb} alt="rest"/>
                            </div>
                            <div className="col-md-9 col-xs-8">
                                <div className="hotel_name">
                                    {item.name}
                                </div>
                                <div className="city_name">{item.locality}</div>
                                <div className="city_name">{item.address}</div>
                            </div>
                        </div>
                        <hr/>
                        <div className="row">
                            <div className="col-sm-3 col-xs-4">
                                <div className="cuisine_block">Cuisine</div>
                                <div className="cuisine_block">Cost Per Two.</div>
                            </div>
                            <div className="col-sm-9 col-xs-8">
                                <div className="cuisine_out">{item.Cuisine[0].name} | {item.Cuisine[1].name}</div>
                                <div className="cuisine_out">Rs. {item.cost}</div>
                            </div>
                        </div>
                    </div>  
                </Link>  
            )
        })
    }else{
        return(
            <div>
                <center><img src="/images/loading.gif" alt="loadericon"/></center>
            </div>
        )
    }
}

const handlePageChange = (props, pageNumber) => {
    props.pageNumber(pageNumber);
    var data = props.listData;
    renderList(props, data);
}

const ListingDisplay = (props) => {
    return(
        <div>
            <div className="row">
                <div className="col-md-12">
                    {renderList(props,props.listdata)}
                </div>
                <center>
                    <Pagination activePage={props.activePage} itemsCountPerPage={props.limit} totalItemsCount={props.totalNoOfItems} pageRangeDisplayed={6} onChange={(pageNumber) => {handlePageChange(props,pageNumber)}}/>
                </center>
            </div>
        </div>
    )
}

export default ListingDisplay;