import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import {withRouter} from 'react-router-dom';
import GoogleLogin from 'react-google-login';

const furl = "https://github.com/login/oauth/authorize?client_id=ba1c3492c956d2139738";
const surl = "https://github.com/login/oauth/access_token"

class Header extends Component {
    constructor(){
        super()

        this.state={
            username:''
        }
    }

    responsegoogle = (response) => {
        sessionStorage.setItem('username',response.profileObj.name)
        this.setState({username:response.profileObj.name})
    }

    conditional_menu = ()=>{
        if(sessionStorage.getItem('username')===null || sessionStorage.getItem('username')===undefined){
            return(
                <ul className="nav navbar-nav navbar-right">
                    <li>
                        <GoogleLogin 
                            clientId="908261406328-n2cibatfeuhadt8d8mfod8rsif3djpe5.apps.googleusercontent.com"
                            buttonText="login with Google"
                            onSuccess={this.responsegoogle}
                            onFailure={this.responsegoogle}
                            cookiePolicy={'single_host_origin'}
                        />
                    </li>
                    <li><a href="https://github.com/login/oauth/authorize?client_id=ba1c3492c956d2139738">Login With Github</a></li>
                </ul>
            )
        }else{
            return(
                <ul className="nav navbar-nav navbar-right">
                    
                    <li><a href=""><span className="glyphicon glyphicon-user"></span> Welcome {sessionStorage.getItem('username')}</a></li>
                </ul>
            )
        }

    }
    render(){
        return(
            <React.Fragment>
                <nav className="navbar navbar-inverse">
                <div className="container-fluid">
                    <div className="navbar-header">
                    <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                    </button>
                    <Link className="navbar-brand" to="/">Edumato</Link>
                    </div>
                    <div className="collapse navbar-collapse" id="myNavbar">
                    <ul className="nav navbar-nav">
                        <li><Link to="/">Home</Link></li>
                        <li><Link to="/vieworders">Orders</Link></li>
                        
                    </ul>
                    <ul>
                        {this.conditional_menu()}
                    </ul>
                    
                    </div>
                </div>
                </nav>
            </React.Fragment>
        )
    }

    componentDidMount(){
        const code = (this.props.location.search).split('=')[1];
        if(code){
            var requestData={
                code:code
            }
            fetch('https://gitoauthapp.herokuapp.com/oauth',{
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(requestData)
            })
            .then(response => response.json()) 
            .then((data) => {
                console.log(data)
                var result = data.name
                sessionStorage.setItem('username',result)
                this.setState({username:result})
            })
        }
    }
}

export default withRouter(Header);