import React,{Component} from 'react';
import axios from 'axios';

let sorturl="https://restaurentapi.herokuapp.com/restaurantList";


class sortFilter extends Component{
    sortfilter = (event) => {
        let sortType = Number(event.target.value);
        let mealType = sessionStorage.getItem('mealid');
        let outurl
        outurl=`${sorturl}/${mealType}?sort=${sortType}`
        console.log(outurl)
        axios.get(outurl)
        .then((response) => {this.props.sortdata(response.data)})
    }
    
    render(){
        return(
            <React.Fragment>
                <div onChange={this.sortfilter} className="filter">
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="sort" id="exampleRadios12" value="1"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios12">
                            Prices low to high
                        </label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="sort" id="exampleRadios13" value="-1"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios13">
                            Prices High to low
                        </label>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default sortFilter;