import React from 'react';
import {withRouter} from 'react-router-dom';
import {Link} from 'react-router-dom'

const BookingView = (props) => {
    const renderTable = ({orderdata}) => {
        if(orderdata){
            return orderdata.map((item) => {
                return(
                    <tr>
                        <td>{item.order_id}</td>
                         <td>{item.rest_name}</td>
                         <td>{item.name}</td>
                         <td>{item.phone}</td>
                         <td>{item.address}</td>
                         <td>{item.person}</td>
                        <td>{item.total_cost}</td>
                    </tr>
                )
            })
        }
    }


    return(
    
        <div className="container">
            <center><h3>Orders Details</h3></center>
            <table className="table table-responsive">
                <thead>
                    <tr>
                        <th>Order Id</th>
                        <th>Rest Name</th>
                        <th>Name</th>
                        <th>Phone</th>
                        <th>Address</th>
                        <th>No. Person</th>
                        <th>Total cost</th>
                    </tr>
                </thead>
                <tbody>
                    {renderTable(props)}
                </tbody>
            </table>
            <center><Link to="/admin" className="btn btn-danger">Back</Link></center>
        </div>
    )
}

export default withRouter(BookingView);