import React,{Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';

const restUrl= "https://restaurentapi.herokuapp.com/restaurantDetails";
const placeorder = "https://restaurentapi.herokuapp.com/placeorder";

class Placeorder extends Component{
    constructor(){
        super()

        this.state={
            _id: Math.floor(Math.random()*10000),
            rest_name:'',
            name:sessionStorage.getItem('username'),
            phone:'',
            person: 1,
            address:'',
            cost:'',
            total_cost:''
        }

    }

    handleChangeName = (event) => {
        this.setState({name:event.target.value})
    }

    handleChangePhone =(event) => {
        this.setState({phone:event.target.value})
    }

    handleChangePerson =(event) => {
        var cost = this.state.cost
        var person = event.target.value
        this.setState({person:event.target.value,total_cost:cost*person})
    }

    handleChangeAddress =(event) => {
        this.setState({address:event.target.value})
    }

    handleSubmit = () => {
        if(sessionStorage.getItem('username')===null || sessionStorage.getItem('username')===undefined){
            alert("Login To place Order")
        }
        else{
            fetch(placeorder,{
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(this.state)
            })
            .then((this.props.history.push(`/details/${this.props.match.params.id}`)))
            alert("Your Order Placed sussesfully")
        }
    }

    async componentDidMount(){
        let restid= this.props.match.params.id;
        let response = await axios.get(`${restUrl}/${restid}`)
        let cost = response.data[0].cost/2
        this.setState({rest_name:response.data[0].name,cost:cost,total_cost:cost})
    }

    render(){
        return(
            <div className="container">
                <div className="panel panel-info">
                    <div className="panel-heading">
                        <center><h4>Place Your Order for {this.state.rest_name}</h4></center>
                    </div>
                    <div className="panel-body">
                        <div className="form-group">
                            <label>_Id</label>
                            <input type="text" name="_id" value={this.state._id} readOnly
                            className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Rest Name</label>
                            <input type="text" name="rest_name" value={this.state.rest_name} readOnly
                            className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Name</label>
                            <input type="text" name="name" value={this.state.name}
                            className="form-control" onChange={this.handleChangeName}/>
                        </div>
                        <div className="form-group">
                            <label>Phone</label>
                            <input type="text" name="phone" value={this.state.phone}
                            className="form-control" onChange={this.handleChangePhone}/>
                        </div>
                        <div className="form-group">
                            <label>Address</label>
                            <input type="text" name="address" value={this.state.address}
                            className="form-control" onChange={this.handleChangeAddress}/>
                        </div>
                        <div className="form-group">
                            <label>Person</label>
                            <select type="text" name="person" value={this.state.person}
                            className="form-control" onChange={this.handleChangePerson}>
                                <option value="1">One</option>
                                <option value="2" default>Two</option>
                                <option value="3">Three</option>
                                <option value="4">Four</option>
                                <option value="5">Five</option>
                                <option value="6">Six</option>
                            </select>
                        </div>
                        <div className="form-group">
                            <label>Total Cost</label>
                            <input type="text" name="total_cost" value={this.state.total_cost} readOnly
                            className="form-control"/>
                        </div>
                        <center>
                            <Link to={`/details/${this.props.match.params.id}`} className="btn btn-danger">Back</Link> &nbsp;
                            <button className="btn btn-success" onClick={this.handleSubmit}>Submit</button>
                        </center>
                    </div>
                </div>
            </div>
        )
    }
}

export default Placeorder;