import React from 'react';
import {Link} from 'react-router-dom';

const Admin = () =>{
    return(
        <React.Fragment>
            <div className="container">
                <div className="jumbotron">
                    <center><h2>Edumato Admin Page</h2></center>
                </div>
                <div className="row">
                    <div className="col-md-3 col-sm-6">
                        <Link to="/admin/addrest" className="btn btn-primary">
                            <img src="/images/Add.png" alt="Add image"/>
                            <h3>Add Restaurent</h3>
                        </Link>
                    </div>
                    <div className="col-md-3 col-sm-6">
                        <Link to="/admin/deleterest" className="btn btn-danger">
                            <img src="/images/delete.jpg" alt="Update image"/>
                            <h3>Delete Restaurent</h3>
                        </Link>
                    </div>
                    <div className="col-md-3 col-sm-6">
                        <Link to="/admin/updaterestid" className="btn btn-primary">
                            <img src="/images/Update.jpg" alt="Update image"/>
                            <h3>Update Restaurent</h3>
                        </Link>
                    </div>
                    <div className="col-md-3 col-sm-6">
                        <Link to="/admin/vieworders" className="btn btn-primary">
                            <img src="/images/order.jpg" alt="Update image"/>
                            <h3>View Orders</h3>
                        </Link>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Admin;