import React from 'react';
import './QuickSearch.css';
import {Link} from 'react-router-dom';

const QuickDisplay =(props)=>{

    const renderList = ({quickData}) => {
        if(quickData){
            return quickData.map((item) => {
                return(
                    <Link to={`/list/${item.mealtype}`} key={item._id}>
                        <div className="tilecontainer" >
                            <div className="tileComponent1">
                                <img alt={item.name} src ={item.thumb} className="imageclass"/>
                            </div>
                            <div className="tileComponent2">
                                <div className="componentHeading">
                                    {item.name}
                                </div>
                                <div className="componentSubHeading">
                                    Start your day with exclusive {item.name} option
                                </div>
                            </div>
                        </div>
                    </Link>
                )
            })
        }else{
            return(
                <div>
                    <center><img src="/images/loading.gif" alt="loadericon"/></center>
                </div>
            )
        }
    }

    return(
        <React.Fragment>
            <div className="quicksearchcontainer">
                <div className="quickheading">
                    Quick Search
                </div>
                <div className="quicksubheading">
                    Discover restaurants by Meal Type
                </div>
                <div>
                    {renderList(props)}
                </div> 
            </div>
        </React.Fragment>
    )
} 

export default QuickDisplay;