import React,{Component} from 'react';
import axios from 'axios';

const cusineurl="https://restaurentapi.herokuapp.com/restaurantList";


class CuisineFilter extends Component{
    cuisinefilter = (event) => {
        let cuisineType = event.target.value;
        let mealType = sessionStorage.getItem('mealid');
        let curl;
        if(cuisineType==''){
            curl= `${cusineurl}/${mealType}`
        }else{
            curl= `${cusineurl}/${mealType}?cuisine=${cuisineType}`
        }
        axios.get(curl)
        .then((response) => {this.props.cuisinedata(response.data)})
    
    }
    
    render(){
        return(
            <React.Fragment>
                <div onChange={this.cuisinefilter} className="filter">
                <div className="form-check">
                        <input className="form-check-input" type="radio" name="CuisineFilter" id="exampleRadios6" value=""/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios6">
                            All
                        </label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="CuisineFilter" id="exampleRadios7" value="1"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios7">
                            North Indian
                        </label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="CuisineFilter" id="exampleRadios8" value="2"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios8">
                            South Indian
                        </label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="CuisineFilter" id="exampleRadios9" value="3"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios9">
                            Chinese
                        </label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="CuisineFilter" id="exampleRadios10" value="4"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios10">
                            Fast Food
                        </label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="CuisineFilter" id="exampleRadios11" value="5"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios11">
                            Street Food
                        </label>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default CuisineFilter;