import React from 'react';
import {BrowserRouter,Route} from 'react-router-dom';
import Home from './Home/Home';
import Header from './header';
import Footer from './footer';
import Listing from './Listing/Listing';
import RestDetails from './RestDetail/RestList';
import Placeorder from './order/bookingForm';
import AdminPage from './admin/Admin' ;
import AddRest from './admin/AddRest';
import UpdateRestid from './admin/SearchUpdateRest';
import UpdateRest from './admin/UpdateRest';
import BookingOrders from './admin/BookingApi';
import ViewOrders from './order/OrderApi';
import DeleteRest from './admin/Deleterest';

const Routing = () =>{
    return(
        <BrowserRouter>
            <div>
                <Header/>
                <Route exact path="/" component={Home}></Route>
                <Route path="/list/:id" component={Listing}></Route>
                <Route path="/details/:id" component={RestDetails}></Route>
                <Route path="/booking/:id" component={Placeorder}></Route>
                <Route exact path="/admin" component={AdminPage}></Route>
                <Route path="/admin/addrest" component={AddRest}></Route>
                <Route path="/admin/updaterestid" component={UpdateRestid}></Route>
                <Route path="/admin/updaterest/:id" component={UpdateRest}></Route>
                <Route path="/admin/vieworders" component={BookingOrders}></Route>
                <Route path="/admin/deleterest" component={DeleteRest}></Route>
                <Route path="/vieworders" component={ViewOrders}></Route>
                <Footer/>
            </div>
        </BrowserRouter>
    )
}

export default Routing;