import React,{Component} from 'react';
import './Search.css';
import {withRouter} from 'react-router-dom';

const lurl = "https://restaurentapi.herokuapp.com/location";
const rurl = "https://restaurentapi.herokuapp.com/restaurents?city=";

class Search extends Component{
    constructor(){
        super()

        this.state={
            location:'',
            restaurents:''
        }
    }

    renderCity = (data) => {
        if(data){
            return data.map((item) => {
                return(
                    <option value={item.city} key={item._id}>
                        {item.city_name}
                    </option>
                )
            })
        }
    }

    renderRestaurent = (data) => {
        if(data){
            return data.map((item) => {
                return(
                    <option value={item._id} key={item._id}>
                        {item.name} | {item.locality}
                    </option>
                )
            })
        }
    }

    // Load restaurent wrt to City 
    handleCity = (event) => {
        const cityid = event.target.value
        const  rrurl = `${rurl}${cityid}`
        fetch(rrurl,{Method:'GET'})
        .then((res) =>  res.json())
        .then((data) => {this.setState({restaurents:data})})
    }

    handleRest =(event) => {
        this.props.history.push(`/details/${event.target.value}`)
    }

    render(){
        return(
            <div className="img-responsive1 img-responsive">
                <div className="logo">
                    e!
                </div>
                <div className="heading">
                    Find the best Restaurants, Cafes, and Bars
                </div>
                <div className="row">
                    <div className="locationselector">
                        <select id="dropdown" onChange={this.handleCity}>
                            <option>Select City</option>
                            {this.renderCity(this.state.location)}
                        </select>
                        <select className="restinput" onChange={this.handleRest}>
                            <option>Select Restaurants</option>
                            {this.renderRestaurent(this.state.restaurents)}
                        </select>
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount(){
        fetch(lurl,{Method:'GET'})
        .then((res) =>  res.json())
        .then((data) => {this.setState({location:data})})
    }
}

export default withRouter(Search);