import React,{Component} from 'react';
import axios from 'axios';

const costUrl = "https://restaurentapi.herokuapp.com/restaurantList"


class CostFilter extends Component{

    costfilter = (event) => {
        let costvalue = (event.target.value).split(',');
        let lcost = costvalue[0];
        let hcost = costvalue[1];
        let mealType = sessionStorage.getItem('mealid');

        let curl;
        if(costvalue ===''){
            curl = `${costUrl}/${mealType}`;
        }else{
            curl = `${costUrl}/${mealType}?hcost=${hcost}&lcost=${lcost}`;
        }
        axios.get(curl)
        .then((response) => {this.props.costdata(response.data)})
    
    }
    render(){
        return(
            <React.Fragment>
                <div onChange={this.costfilter} className="filter">
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="exampleRadios" id="exampleRadios1" value=""/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios1">
                            All
                        </label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="exampleRadios" id="exampleRadios2" value="100,300"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios2">
                            Rs.100 to Rs.300
                        </label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="exampleRadios" id="exampleRadios3" value="301,500"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios3">
                            Rs.301 to Rs.500
                        </label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="exampleRadios" id="exampleRadios4" value="501,800"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios4">
                            Rs.501 to Rs.800
                        </label>
                    </div>
                    <div className="form-check">
                        <input className="form-check-input" type="radio" name="exampleRadios" id="exampleRadios5" value="801,1000"/>&nbsp;&nbsp;
                        <label className="form-check-label" for="exampleRadios5">
                            Rs.801 to Rs.1000
                        </label>
                    </div>
                </div>
                
            </React.Fragment>
        )
    }
}

export default CostFilter;